.nama_page {
  overflow: hidden;
  background-color: var(--warna-biru);
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: var(--warna-biru);
  justify-content: center;
  text-transform: uppercase;
}
@media screen and (min-width: 30rem) {
  .nama_page {
    font-size: 1.5rem;
  }
}
@media screen and (min-width: 40rem) {
  .nama_page {
    font-size: 2rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .nama_page {
    font-size: 2.5rem;
  }
}
.nama_page .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 64rem) {
  .nama_page .cursor {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    border: 1px solid var(--warna-cream);
    z-index: 100;
  }
  .nama_page .cursor.masuk {
    display: flex;
    align-items: center;
    justify-content: center;
    border: var(--warna-biru) solid 1px;
    font-size: 0.75rem;
    text-align: center;
  }
}
.nama_page .container {
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 22rem;
  height: 22rem;
}
@media screen and (min-width: 30rem) {
  .nama_page .container {
    width: 32rem;
    height: 32rem;
  }
}
@media screen and (min-width: 40rem) {
  .nama_page .container {
    width: 42rem;
    height: 42rem;
  }
}
@media screen and (min-width: 64rem) and (min-height: 37.5rem) {
  .nama_page .container {
    width: 40rem;
    height: 40rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .nama_page .container {
    width: 52rem;
    height: 52rem;
  }
}
.nama_page .container .nama {
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nama_page .container .nama span {
  width: 100%;
}
.nama_page .container .bunder {
  position: absolute;
  background-color: var(--warna-cream);
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
  z-index: 1;
}
@media screen and (min-width: 30rem) {
  .nama_page .container .bunder {
    width: 30rem;
    height: 30rem;
  }
}
@media screen and (min-width: 40rem) {
  .nama_page .container .bunder {
    width: 40rem;
    height: 40rem;
  }
}
@media screen and (min-width: 64rem) and (min-height: 37.5rem) {
  .nama_page .container .bunder {
    width: 38rem;
    height: 38rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .nama_page .container .bunder {
    width: 50rem;
    height: 50rem;
  }
}
.nama_page .container .bunder2 {
  position: absolute;
  border: var(--warna-cream) solid 2px;
  border-radius: 50%;
  width: 22rem;
  height: 22rem;
  z-index: 1;
}
@media screen and (min-width: 30rem) {
  .nama_page .container .bunder2 {
    width: 32rem;
    height: 32rem;
  }
}
@media screen and (min-width: 40rem) {
  .nama_page .container .bunder2 {
    width: 42rem;
    height: 42rem;
  }
}
@media screen and (min-width: 64rem) and (min-height: 37.5rem) {
  .nama_page .container .bunder2 {
    width: 40rem;
    height: 40rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .nama_page .container .bunder2 {
    width: 52rem;
    height: 52rem;
  }
}